









































































































import {Component, Prop, Vue, Watch} from "vue-property-decorator"
import Portal from "@/managers/portal"
import Profile from "@/managers/profile"
import {IBooking, IPayment, IStudentProfile, ITrainerProfile, ITrainingSession, TrainingSessionDuration, TrainingSessionType} from "@chaosinsight/yogipeople-portalclient"
import {IPaymentMethod} from "@chaosinsight/yogipeople-portalclient/dist/extensions/payments"
import PageBackground from "@/components/PageBackground.vue"
import MenuAside from "@/components/MenuAside.vue"
import MenuItems from "@/components/MenuItems.vue"
import StudentBookings from "@/components/StudentBookings.vue"
import HeroText2 from "@/components/HeroText2.vue"
import TrainLive from "@/components/TrainLive.vue"
import TrainSpecific from "@/components/TrainSpecific.vue"
import BookingCountdown from "@/components/BookingCountdown.vue"
import HyModal from "@/components/HyModal.vue"
import FeedbackStudent from "@/components/FeedbackStudent.vue"
import PageBgRotator from "@/components/PageBgRotator.vue"
import CardPlans from "@/components/CardPlans.vue"
import i18n from "../i18n"
import OtpInput from "@bachdgvn/vue-otp-input"
import {VueTelInput} from "vue-tel-input"
import { TranslateResult } from "vue-i18n"
import NotificationMessage from "@/components/NotificationMessage.vue"

@Component({ components: { BookingCountdown, CardPlans, FeedbackStudent, HyModal, MenuAside, MenuItems, StudentBookings,
	TrainLive, TrainSpecific, PageBackground, PageBgRotator, HeroText2, OtpInput, VueTelInput, NotificationMessage} })
export default class StudentLanding extends Vue {
	@Prop()
	public profile!: IStudentProfile

	@Prop()
	public feedbackType!: string
	@Prop()
	public sessionData!: ITrainingSession

	@Prop()
	public blur!: boolean

	public systemTime: string | null = null

	public booking: IBooking | null = null
	private trainer: ITrainerProfile | null = null

	private selectedBookingId: number | null = null
	private selectedTrainerId: string | null = null

	private canceledBookingId: number | null = null
	private notifyMsg: string | null = null
	private mobile: boolean | null = null
	private changeNumber: boolean | null = null
	private listBookings: boolean | null = null
	private prevTrainers: boolean | null = null
	private plans: boolean | null = null
	private menuOpen: boolean | null = null
	private activeItem: string | null = null
	private showBilling: boolean | null = null
	private modal: boolean | null = null
	private modal1: boolean | null = null
	private modalType: string | null = null
	private modalText: TranslateResult | null = null
	private modalSlot: TranslateResult | null = i18n.t("studentLanding.modalSlot")
	private cardLength: number | null = null
	private smPanels: boolean | null = null
	private savedCards: IPaymentMethod[] | null = null
	private reloadSessionsHistory: boolean | null = null
	private emailVerified: string | null = null
	private closedFeedback: string | null = null
	private VerificationCode: string = ""
	private prevMobile: string | null = ""
	private countrySym: string | null = ""
	private countryCode: string = ""
	private mobileNumber: string = ""
	private menuItems: object[] = [
		{ label: i18n.t("myAccount"), name: "account"},
		{ label: "My Subscription", name: "subscriptions"},
		{ label: i18n.t("myPayment"), name: "payment"},
		{ label: i18n.t("mySessions"), name: "sessions"},
		{ label: i18n.t("help"), name: "help"},
		{ label: i18n.t("legal"), name: "legal"},
		{ label: i18n.t("signout"), name: "signout"}
	]

	private created(): void {
		this.prevMobile = window.localStorage.getItem("mobile")
		this.countrySym = window.localStorage.getItem("countrySym")
		this.closedFeedback = window.localStorage.getItem("closedFeedback")
		if (this.closedFeedback === "closed") {
			this.modal = true
			this.feedbackType = "complete"
		} else
			if (!!this.feedbackType)
				this.modal = true
		this.checkPayment()
		this.checkMobileVerification()
		this.getSystemTime()
			.then(time => this.systemTime = time)
					}
	private checkMobileVerification(): void {
			Portal.client.call.student.me()
					.response
					.then(response => {
						if (response.IsMobileVerified === true)
							this.modal1 = false
						})
	}
	private onInput({ country }: any): void {
				this.countryCode = country.dialCode.toString()
			}
	private get isEmailVerified(): boolean | null {
		return Profile.isReady
			? Profile.profile!.IsMobileVerified
			: null
	}
	private handleOnComplete( value: any): void {
				this.VerificationCode = value
				}
	private confirmSms(): void {
		Portal.client.call.account.verifyMobile(this.VerificationCode)
				.response
				.then(res => {
					Portal.client.call.student.me()
					.response
					.then(response => { })

					this.confirmMobileModal()
				})
				.catch(err => {
					this.notifyMsg = err
				})
	}
	private confirmMobileModal(): void {
		this.modalText = `Phone number confirmed!`
		this.modal1 = true
		window.setTimeout(() => this.closeModal(), 1200 )
	}
	private checkPayment(): void {
		Portal.client.call.payments.paymentMethodsGet("card")
				.response
				.then(res => {
					this.savedCards = [].slice.call(res)
					this.cardLength = this.savedCards.length
						})

	}
	@Watch("feedbackType")
	private feedback(value: string): void {
		if (!!value)
			this.modal = true
	}

	@Watch("trainer")
	private showSmallPanel(value: ITrainerProfile): void {
		if (value)
			this.smPanels = true
	}
	private mounted(): void {
		if (!!this.feedbackType || !this.isEmailVerified)
			this.modal1 = true
	}

	/**
	 * Get server time
	 */

	private getSystemTime(): Promise<string> {
		return Portal.client.call.system.Time()
			.response
			.then(res => res.Time)
	}

	/**
	 * Is list(s) exists: List Bookings or List Previous Teachers
	 */

	private listUpcoming(listUpcoming: boolean): void {
		this.listBookings = listUpcoming
		this.canceledBookingId = null
	}

	private listTrainers(prevTrainers: boolean): void {
		this.prevTrainers = prevTrainers
	}

	/**
	 * Listen start/cancel booking and $emit to Student
	 */

	private startBookingSession(booking: IBooking): void {
		this.$emit("startBookingSession", booking)
	}

	private cancelBooking(booking: IBooking): void {
		this.showDefaultPanel()
		this.smPanels = null
		this.canceledBookingId = Number(booking)
		this.$emit("cancelBooking", booking)
	}

	/**
	 * Display time until upcoming booking
	 */

	private timeUntilBooking(booking: IBooking): void {
		this.getSystemTime()
			.then(systemTime => {
				this.systemTime = systemTime
				this.booking = booking
				this.selectedBookingId = booking.Id
				this.trainer = null
				this.selectedTrainerId = null
				this.smPanels = true
			})
	}

	/**
	 * Open menu item page
	 */

	private openActiveItem(item: string): void {
		item === "signout"
			? this.$router.push("/")
			: this.activeItem = item
		if (item === "signout") {
			window.localStorage.setItem("Login", "Logout")
			window.localStorage.removeItem("email")
			window.localStorage.removeItem("pass")
			window.localStorage.removeItem("isStudent")
			window.localStorage.removeItem("isReady")
			window.localStorage.removeItem("firstTimeUser")
			window.localStorage.removeItem("name")
			window.localStorage.removeItem("Portal")
			window.localStorage.removeItem("isSubscribed") }
		}

	/**
	 * Set trainer data for specific trainer request
	 */

	private trainerData(trainer: ITrainerProfile): void {
		this.trainer = trainer
		this.selectedTrainerId = trainer.Id
		this.booking = null
		this.selectedBookingId = null
	}

	/**
	 * Show default panel (live session request)
	 */

	private showDefaultPanel(): void {
		this.booking = null
		this.selectedBookingId = null
		this.trainer = null
		this.selectedTrainerId = null
		this.smPanels = null
	}

	/**
	 * Set modal text and open modal
	 */

	private modalConfirmBooking(duration: TrainingSessionDuration, type: TrainingSessionType, termin: string, name?: string): void {
		this.modalText = `Confirmed ${duration === 30 ? 25 : 55}m ${this.capitalize(TrainingSessionType[type])} session ${name ? "with " + name : ""} for ${termin}`
		this.modal1 = true
		window.setTimeout(() => this.closeModal(), 1200 )
	}

	private modalErr(msg: string): void {
		this.showDefaultPanel()
		this.modalText = msg
		this.modal1 = true
		this.modalType = "error"
		window.setTimeout(() => this.closeModal(), 1500 )
	}

	private modalEmailVerify(modalText: string): void {
		this.mobile = true
	}

	private closeModal(): void {
		this.modal1 = null
		this.modalType = null
		// this.$router.push("/home")
		if (this.showBilling)
			this.$router.push("/card")
		else this.$router.push("/home")
		}
	/**
	 * Send verification SMS
	 */

	private sendVerificationMobile(): void {
		this.modalSlot = ""
		this.modal1 = true
		this.mobile = true
		Portal.client.call.account.requestMobileVerification()
		.response
		.then(() => {
			if (this.cardLength === 0 || this.cardLength === null)
				this.showBilling = true
			else this.showBilling = false
		})
		.catch(reason => {
			this.notifyMsg = reason
		})
	}
	// Change Mobile Number

	private changeMobileNumber(): void {
		this.modalSlot = ""
		this.changeNumber = true
		this.mobile = null
	}
	private SubmitUpdateMobile(): void {
		Portal.client.call.account.updateMobileNumber(this.countryCode, this.mobileNumber)
			.response
			.then(res => {
				this.mobile = true
				this.changeNumber = false
				this.prevMobile = this.mobileNumber
				this.countrySym = this.countryCode
			})
			.catch(err => {
				this.notifyMsg = err
			})
		}
	// Cancel Update Number
	private cancelPhoneUpdate(): void {
		this.mobile = true
		this.changeNumber = false
		}
		// Resend OTP on Mobile
	private resendOTP(): void {
		Portal.client.call.account.requestMobileVerification()
		.response
		.then(() => {
			this.notifyMsg = "Successfully resent SMS code"
		})
		.catch(reason => {
			this.notifyMsg = reason
		})
	}

	/**
	 * Capitalize word
	 */

	private capitalize(str: string): string {
		return str.charAt(0).toUpperCase() + str.slice(1)
	}

	/**
	 * Set blur effect for page bg
	 */

	private bgBlur(): boolean | null {
		return this.modal1 || (this.smPanels && document.body.clientWidth < 960)
	}

	/**
	 * Listen "updateRating" from MenuItems and emit to Student
	 */

	private updateRating(session: ITrainingSession): void {
		this.$emit("updateRating", session)
	}

	/**
	 * Close feedback modal
	 */

	private closeFeedback(): void {
		this.modal = false
		this.$emit("closeFeedback")
	}
}
