








import {Component, Vue} from "vue-property-decorator"
import {IPayment, IStudentProfile, ITrainerProfile} from "@chaosinsight/yogipeople-portalclient"
import {IPaymentMethod} from "@chaosinsight/yogipeople-portalclient/dist/extensions/payments"
import Portal from "@/managers/portal"
import Profile from "@/managers/profile"
import Student from "@/components/Student.vue"
import Trainer from "@/components/Trainer.vue"
import PendingMessage from "@/components/PendingMessage.vue"
import PushNotifications from "@/components/PushNotifications.vue"

@Component({ components: { Student, Trainer, PendingMessage, PushNotifications } })
export default class Home extends Vue {
	private isAuth: string | null = null
	private isAuthenticated: string | null = null
	private isStud: string | null = null
	private isTrainer: string | null = null
	private isProfile: string | null = null
	private clientPortal: string | null = null
	private email: string | null = null
	private pass: string | null = null
	private savedCards: IPaymentMethod[] | null = null
	public get isProfileReady(): boolean {
		return Profile.isReady
	}

	public get isStudent(): boolean | null {
		return Profile.isStudent
	}

	public get profile(): IStudentProfile | ITrainerProfile | null {
		return Profile.profile
	}
	public mounted(): void {
		if (window.localStorage.getItem("Login") === "Logout")
			this.$router.push("/signin")
		this.isStud = window.localStorage.getItem("Login")
		if (JSON.parse(JSON.stringify(localStorage.getItem("Login"))) === "Loggedin") {
			const email = JSON.parse(JSON.stringify(localStorage.getItem("email")))
			const pass = JSON.parse(JSON.stringify(localStorage.getItem("pass")))
			Portal.login(email, pass)
			.then((res: any) => {
				if (Portal.isAuthenticated)
					this.$router.push("/home")
			})
			.catch((res: any) => {
			})
		} else
			if (Portal.isAuthenticated)
				this.$router.push("/home")
			else
				this.$router.push("/signin")
		}
	private profileUpdate(): void {
		Profile.update()
	}
}
