



















import {Component, Prop, Vue} from "vue-property-decorator"
import {IBooking, IStudentProfile, ITrainingSession, TrainingSessionState} from "@chaosinsight/yogipeople-portalclient"
import Portal from "@/managers/portal"
import TrainingSession from "@/components/TrainingSession.vue"
import StudentLanding from "@/components/StudentLanding.vue"
import BookingCountdown from "@/components/BookingCountdown.vue"

@Component({ components: { BookingCountdown, TrainingSession, StudentLanding } })
export default class Student extends Vue {
	@Prop()
	public profile!: IStudentProfile

	public booking: IBooking | null = null

	public systemTime: string | null = null
	public serverTimeDiff: number | null = null

	public session: ITrainingSession | null = null

	private feedbackType: string | null = null
	private sessionData: ITrainingSession | null = null

	private created(): void {
		this.checkForSession()
		this.getSystemTime()
			.then(time => this.systemTime = time)
	}

	/**
	 * Get server time
	 */

	private getSystemTime(): Promise<string> {
		return Portal.client.call.system.Time()
			.response
			.then(res => res.Time)
	}

	/**
	 * Start session from booking
	 */

	private startBookingSession(booking: IBooking) {
		Portal.client.call.trainingSession.createFromBooking(booking.Id)
			.response
			.then(() => this.checkForSession())
			.catch(() => this.checkForSession())
	}

	/**
	 * Cancel upcoming booking
	 */

	private cancelBooking(id: string) {
		Portal.client.call.booking.put(id)
			.response
			.then(() => this.booking = null)
	}

	/**
	 * Check for session
	 */

	private checkForSession(): Promise<boolean> {
		return Portal.client.call.trainingSession.getOpen()
			.response
			.then(session => {
				if ([TrainingSessionState.Expired, TrainingSessionState.OtherUserCompleted].indexOf(session.State) !== -1)
					this.closeSession("completeOnLogin", session)
				else if (session.State === TrainingSessionState.OtherUserCancelled)
					this.closeSession("cancelOnLogin", session)
				else if (!!session.Token) {
					this.session = session
					this.booking = null
					return true
				}
				return false
			})
			.catch(() => false)
	}

	private closeSession(reason: string, session?: ITrainingSession): void {
		this.sessionData = session ? session : this.session
		this.session = null
		this.feedbackType = reason
	}

	private closeFeedback(): void {
		this.feedbackType = null
		this.sessionData = null
	}

	/**
	 * Update rating for session in "My Sessions" history list
	 */

	private updateRating(sessionData: ITrainingSession): void {
		this.feedbackType = "updateRating"
		this.sessionData = sessionData
	}
}
